<template>
    <div class="myCollect">
        <van-nav-bar :fixed="true" title="我的收藏" left-text="返回" left-arrow @click-left="$router.go(-1)" />

        <div class="goods">
            <div class="goods-item" v-for="(item, index) in likeData" :key="index">
                <div>
                    <img class="auto-img" :src="item.smallImg" alt="">
                </div>
                <div class="goods-name">{{ item.name }}</div>

                <div class="goods-price">
                    <span class="price">￥{{ item.price }}</span>
                    <span class="del" @click="del(item.pid, index)">
                        <van-icon name="delete" />
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Toast } from "vant";

    export default {
        data() {
            return {
                // 收藏商品数据
                likeData: null,
            };
        },

        methods: {

            // 获取收藏的商品
            getLike() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findAllLike",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 2000) {

                        this.likeData = res.data.result;

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 删除收藏商品
            del(pid, index) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/notlike",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        pid,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 900) {
                        Toast.success(res.data.msg);

                        this.likeData.splice(index, 1);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            }
        },

        created() {

            // 获取收藏的商品
            this.getLike();

        }
    }
</script>

<style lang="less" scoped>
    .van-nav-bar{
        height: 45px;
    }
    
    .goods{
        display: flex;
        padding: 45px 14px 14px;
        color: #646566;
        justify-content: space-between;
        flex-wrap: wrap;

        .goods-item{
            width: 48%;
            margin-top: 14px;
            padding: 10px;
            background-color: #fff;
            box-sizing: border-box;

            .goods-name{
                margin: 10px 0;
                font-size: 15px;
            }

            .goods-price{
                display: flex;
                font-size: 15px;
                justify-content: space-between;
                align-items: center;

                .price{
                    color: #1989FA;
                }
            }
        }
    }
</style>